.vux-slider {
  overflow: hidden;
  position: relative;
}
.vux-slider > .vux-indicator,
.vux-slider .vux-indicator-right {
  position: absolute;
  right: 15px;
  bottom: 10px;
}
.vux-slider > .vux-indicator > a,
.vux-slider .vux-indicator-right > a {
  float: left;
  margin-left: 6px;
}
.vux-slider > .vux-indicator > a > .vux-icon-dot,
.vux-slider .vux-indicator-right > a > .vux-icon-dot {
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background-color: #d0cdd1;
}
.vux-slider > .vux-indicator > a > .vux-icon-dot.active,
.vux-slider .vux-indicator-right > a > .vux-icon-dot.active {
  background-color: #04BE02;
}
.vux-slider > .vux-indicator-center {
  right: 50%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}
.vux-slider > .vux-indicator-left {
  left: 15px;
  right: auto;
}
.vux-slider > .vux-swiper {
  overflow: hidden;
  position: relative;
}
.vux-slider > .vux-swiper > .vux-swiper-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.vux-slider > .vux-swiper > .vux-swiper-item > a {
  display: block;
  width: 100%;
  height: 100%;
}
.vux-slider > .vux-swiper > .vux-swiper-item > a > .vux-img {
  display: block;
  width: 100%;
  height: 100%;
  background: center center no-repeat;
  background-size: cover;
}
.vux-slider > .vux-swiper > .vux-swiper-item > a > .vux-swiper-desc {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 1.4em;
  font-size: 16px;
  padding: 20px 50px 12px 13px;
  margin: 0;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, 0.7)));
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.7) 100%);
  color: #fff;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.5);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}

.vux-x-img, .b-lazy {
  -webkit-transition: opacity 500ms ease-in-out;
  transition: opacity 500ms ease-in-out;
  max-width: 100%;
}
.b-lazy {
  opacity: 0;
}
.b-lazy.b-loaded {
  opacity: 1;
}

.menu .lately{
	background: #fff;
	margin-bottom: 10px;
}
.menu .lately .title h2 {
    padding: 10px;
    color: #999;
    position: relative;
}
.menu .lately .content {
	padding: 10px;
	height: 100px;
	overflow: hidden;
}
.menu .lately .content .box{
    height: 100%;
    position: relative;
    /*width: 9999px;*/
}
.menu .lately .content .item{
	text-decoration: none;
	width: 80px;
	margin-right: 10px;
	float: left;
	position: relative;
	display: inline-block;
}
.menu .lately .content .item img{
	width: 80px;
	height: 80px;
}
.menu .lately .content .item h3{
	text-align: center;
	line-height: 18px;
	color: #333333;
	font-size: 14px;
}
.menu .lately .content:after{
	height: 0;
	width: 0;
	clear: both;
	overflow: hidden;
	content: ' ';
}
.menu .weui-search-bar__label{
	top: 5px !important;
}
.menu .vux-scroller{
	margin-top: 46px;
}
.menu .weui-grids{
	background: #fff;
}
.menu .weui-grids h2{
	padding: 10px;
	color: #999;
	position: relative;
}
.menu .weui-grids h2:after{
	content: " ";
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: 1px;
    border-bottom: 1px solid #D9D9D9;
    color: #D9D9D9;
    -webkit-transform-origin: 0 100%;
    transform-origin: 0 100%;
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
.menu .weui-grid{
	padding: 10px !important;
	text-decoration: none;
	color: #000;
}
.menu .weui-grid .grid-center .vux-badge{
	float: right;
}
